<template>
  <div class="trips-page bg-gray-100 min-h-screen p-4">
    <!-- 页面标题 -->
    <div class="flex justify-between items-center mb-8">
      <h1 class="text-2xl font-bold">行程记录</h1>
      <el-button type="primary" class="bg-blue-500 text-white border-0 font-bold px-6 py-2 rounded-md" @click="loadMoreTrips">
        {{ showAll ? '收起' : '查看更多' }}
      </el-button>
    </div>
    
    <!-- 行程记录列表 -->
    <div class="bg-white rounded-lg shadow p-5 mb-8">
      <div v-if="displayTrips.length === 0" class="text-center py-8">
        <el-icon size="48" class="text-gray-400 mb-4"><Timer /></el-icon>
        <h3 class="text-lg font-medium text-gray-500">暂无行程记录</h3>
        <p class="text-gray-400 mt-2">您还没有任何骑行记录</p>
        <router-link to="/rent">
          <el-button type="primary" class="mt-4">立即用车</el-button>
        </router-link>
      </div>
      <div v-else>
        <div v-for="(trip, index) in displayTrips" :key="index" class="trip-item border-b border-gray-100 py-4 last:border-0">
          <div class="flex justify-between items-center">
            <div class="flex-1">
              <h3 class="text-lg font-medium">{{ trip.location }}</h3>
              <p class="text-sm text-gray-500 mt-1">{{ trip.date }}</p>
              <div class="flex items-center mt-2">
                <span class="text-sm text-gray-600 mr-4">骑行时间: {{ trip.duration }}</span>
                <span class="text-sm text-gray-600">骑行距离: {{ trip.distance }}</span>
              </div>
            </div>
            <div class="text-right">
              <span class="text-lg font-medium text-blue-600">{{ trip.price }}</span>
              <div class="text-xs text-gray-500 mt-1">{{ trip.status }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed } from 'vue';
import { Timer } from '@element-plus/icons-vue';

// 模拟数据
const allTrips = [
  {
    location: '上海市徐汇区复兴中路',
    date: '2023-04-05 14:30',
    duration: '25分钟',
    distance: '3.5公里',
    price: '¥6.50',
    status: '已完成'
  },
  {
    location: '上海市静安区南京西路',
    date: '2023-04-03 09:15',
    duration: '18分钟',
    distance: '2.1公里',
    price: '¥4.00',
    status: '已完成'
  },
  {
    location: '上海市黄浦区人民广场',
    date: '2023-03-28 19:45',
    duration: '32分钟',
    distance: '4.2公里',
    price: '¥8.50',
    status: '已完成'
  },
  {
    location: '上海市浦东新区陆家嘴',
    date: '2023-03-25 10:20',
    duration: '45分钟',
    distance: '5.8公里',
    price: '¥11.00',
    status: '已完成'
  },
  {
    location: '上海市杨浦区五角场',
    date: '2023-03-20 16:40',
    duration: '22分钟',
    distance: '2.9公里',
    price: '¥5.50',
    status: '已完成'
  }
];

const trips = ref(allTrips);
const showAll = ref(false);
const initialDisplayCount = 3;

// 计算显示的行程记录
const displayTrips = computed(() => {
  return showAll.value ? trips.value : trips.value.slice(0, initialDisplayCount);
});

// 加载更多行程记录
function loadMoreTrips() {
  showAll.value = !showAll.value;
}
</script>

<style scoped>
.trip-item:hover {
  background-color: rgba(243, 244, 246, 0.4);
}
</style> 